
@media screen and (max-width: 700px){
	.gameButtons{
		flex-direction: column;
	}
	.btnInGameBar{
		width: 300px; height: 70px;
	}
}
@media screen and (min-width: 700px){
	.gameButtons{
		flex-direction: row;
		transform: translate(0, 450%);
	}
	.btnInGameBar{
		width: 200px; height: 50px;
	}

}

.gameButtons{
	display: flex;
	align-items: center;
	justify-content: center;
}

.btnInGameBar{
	margin: 5pt;
	
	background-size: cover;
	background-position: center;
	border-color: red;
}

.btnClickDefender{
	background-image: url(./images/btnCD.png);
}
.btnCrystalPlanets{
	background-image: url(./images/btnCP.png);
}
.btnWayToSuccess{
	background-image: url(./images/btnWTS.png);
}